html {
    /*
    rem em
    em   相当于父元素的 font-size
    rem  相当于根元素 html 的 font-size, r 就是 root 的意思
    浏览器默认的 font-size 是 16px
    所以 html 的 font-size 设置为 62.5%，就相当于将 html 的 font-size 设置成了 16px * 0.625 = 10px
    那么 1rem 就等于 10px
  */
    font-size: 62.5%;
}

html body #root .App {
    min-height: 100vh;
}